  display: block; }

/*
Layout styles.
*/
body {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5; }

.menu-container {
  text-align: center; }

nav ul {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0; }

.nav {
  align-self: center;
  background: #333745;
  color: #a5afd7;
  display: grid;
  grid-row: 1 /2;
  margin: 2rem auto 0;
  text-align: left; }
  .nav a {
    display: block;
    padding: 0 16px;
    line-height: inherit;
    cursor: pointer; }

.nav__menu {
  line-height: 45px;
  font-weight: 700;
  text-transform: uppercase; }

.nav__menu-item {
  display: inline-block;
  position: relative; }
  .nav__menu-item:hover {
    background-color: #3c4251; }
    .nav__menu-item:hover .nav__submenu {
      display: block; }

.nav__submenu {
  font-weight: 300;
  text-transform: none;
  display: none;
  position: absolute;
  width: 16rem;
  background-color: #3c4251; }

.nav__submenu-item:hover {
  background: #20222B; }

.header {
  background: #20222B;
  color: white;
  font-family: "Lato", sans-serif;
  margin-bottom: 1.6rem;
  padding: 1.6rem; }

.container {
  display: grid;
  grid-template-columns: 10% 10% 10% 20% 30% 10% 10% 10%;
  grid-template-rows: 5rem 5rem; }

.header__title {
  font-size: 3.2rem;
  grid-column: 1 / span 4;
  grid-row-start: 1;
  justify-self: start;
  margin: 0; }

.header__subtitle {
  color: #a5afd7;
  font-size: 1.6rem;
  font-weight: 500;
  grid-column: 1 / span 3;
  grid-row-start: 2;
  justify-self: end;
  margin: .5rem; }

@media (min-width: 45rem) {
  .header {
    margin-bottom: 4.8rem; } }

